import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController } from 'ionic-angular';
import { HubConnection, TransportType } from '@aspnet/signalr-client';

@IonicPage()
@Component({
  selector: 'page-home',
  templateUrl: 'home.html',
})
export class HomePage {

  user: any;
  people: any;
  private isSearchOn: boolean = false;
  private tradeData;
  private contractUpdate;
  Math: any;
  // private connection = new HubConnection('http://127.0.0.1:5000/mxv', { transport: TransportType.LongPolling });
  private connection = new HubConnection('http://125.212.254.113:4444/mxv', { transport: TransportType.LongPolling });

  private textColor = [
    '',
    'text-mxv-normal', // equal,
    'text-secondary', // up,
    'text-danger', // down,
    'text-mxv-ceil', // ceil,
    'text-primary', // floor
  ];

  contracts = [
    { ContractName: 'CoffeeJan18', Price: '18.9', TDoi: 0, TDoipt: '0%', Vol: 1000, Change: 'up' },
    { ContractName: 'CoffeeFeb17', Price: '18.9', TDoi: 0, TDoipt: '0%', Vol: 1000, Change: 'down' },
    { ContractName: 'RubberMar18', Price: '18.9', TDoi: 0, TDoipt: '0%', Vol: 1000, Change: 'down' },
    { ContractName: 'RubberApr18', Price: '18.9', TDoi: 0, TDoipt: '0%', Vol: 1000, Change: 'down' },
    { ContractName: 'CoffeeJan19', Price: '18.9', TDoi: 0, TDoipt: '0%', Vol: 1000, Change: 'down' },
    { ContractName: 'CoffeeFeb17', Price: '18.9', TDoi: 0, TDoipt: '0%', Vol: 1000, Change: 'down' },
    { ContractName: 'CoffeeJan19', Price: '18.9', TDoi: 0, TDoipt: '0%', Vol: 1000, Change: 'down' },
    { ContractName: 'CoffeeJan19', Price: '18.9', TDoi: 0, TDoipt: '0%', Vol: 1000, Change: 'down' },
    { ContractName: 'CoffeeJan19', Price: '18.9', TDoi: 0, TDoipt: '0%', Vol: 1000, Change: 'down' }
  ];

  ngOnInit() {
    console.log('on init page');
  }

  ngOnDestroy() {
    console.log('destroyu page home');
  }
  testCheckboxOpen: boolean;
  testCheckboxResult: any;

  constructor(public navCtrl: NavController, private alertCtrl: AlertController) {
    this.Math = Math;
    this.user = window.localStorage.getItem('user');

    this.connection.on('updateTDB', obj => {
      console.log('update TDB');
      console.log(obj);
      this.contractUpdate = obj;
      const index = this.tradeData.findIndex(s => s.ContractCode === obj.ContractCode);
      this.tradeData[index] = obj;
    });

    this.connection.on('openSession', () => {
      console.log('Open Session');
      this.loadTradingBoardData();
      const alert = this.alertCtrl.create({
        title: 'Phin!',
        subTitle: 'Phin  hot ng!',
        buttons: ['OK']
      });
      alert.present();
    });

    this.connection.on('closeSession', () => {
      const alert = this.alertCtrl.create({
        title: 'Phin!',
        subTitle: ' ng hoc cha m!',
        buttons: ['OK']
      });
      alert.present();
      console.log('Close Session');
    });

    this.connection.on('logout', (data) => {
      console.log(data);
    });

    this.connection.start()
      .then(() => {
        console.log('Connected');
        this.loadTradingBoardData();
      });
  }

  loadTradingBoardData() {
    this.connection.invoke('GetAllTradingBoard').then(data => {
      console.log('Get All Data');
      console.log(data);
      this.tradeData = data;
    });
  }

  logout(): void {
    window.localStorage.removeItem('user');

    this.navCtrl.setRoot('LoginPage');
    this.navCtrl.popToRoot();
  }
  openLoginPage() {
    this.navCtrl.push('LoginPage');
  }

  openListContract(): void {
    this.navCtrl.push('ListContractPage');
  }

  goMenuPage(): void {
    this.navCtrl.setRoot('MenuPage');
  }

  itemSelected(item) {
    this.navCtrl.push('ListBuySellPage');
  }

  toggleDetails() {
    this.isSearchOn = !this.isSearchOn;
  }
}



